import React, { Component, createRef } from "react";
import factory from "../../ethereum/factoryContract";
import Layout from "../../components/layouts";
import { Card, Sticky, Grid, Ref, Segment } from "semantic-ui-react";
import { Link } from "../../routes";
import ProjectCard from "../../components/ProjectCard";

export default class ProjectIndex extends Component {
  constructor(props) {
    super(props);

    this.state = {
      filteredProjects: [],
      projects: [],
      filter: null
    };
  }

  componentDidMount() {
    const projects = this.props.allProjects.filter(
      project => project.status === "deployed" || project.status === "funded"
    );
    this.setState({ projects: projects });
  }

  setProjects = projects => {
    this.setState({ allprojects: projects });
    this.setState({ filteredProjects: projects });
  };

  handleSearch = e => {
    const searchTerm = e.target.value.toLowerCase();
    const newProjects = this.props.allProjects.filter(project =>
      project.title.toLowerCase().includes(searchTerm)
    );
    this.setState({ filteredProjects: newProjects });
  };

  handleFilter = filter => {
    this.setState({ filter: filter });
  };

  contextRef = createRef();

  render() {
    return (
      <div style={{ marginTop: "20px" }}>
        <h1>All Projects</h1>
        <Grid stackable columns={2}>
          <Grid.Column width={4}>
            <Sticky context={this.contextRef}>
              <div
                className="ui vertical menu left floated"
                style={{ marginTop: 20 }}>
                <div className="item">
                  <div className="ui input">
                    <input
                      onChange={this.handleSearch}
                      type="text"
                      placeholder="Search..."
                    />
                  </div>
                </div>
                <div className="item">
                  See only
                  <div className="menu">
                    <a
                      className="item"
                      onClick={() => this.handleFilter("funding")}>
                      Awaiting funding
                    </a>
                    <a
                      className="item"
                      onClick={() => this.handleFilter("funded")}>
                      Funded
                    </a>
                  </div>
                </div>
                <div className="item">
                  Sort By
                  <div className="menu">
                    <a className="item">Supporters</a>
                    <a className="item">Backing</a>
                  </div>
                </div>
              </div>
            </Sticky>
          </Grid.Column>
          <Grid.Column stackable width={12}>
            <Card.Group itemsPerRow={3}>
              {this.state.filteredProjects.map((project, i) => (
                <ProjectCard
                  key={i}
                  project={project}
                  selectProject={this.props.selectProject}
                />
              ))}
            </Card.Group>
          </Grid.Column>
        </Grid>
      </div>
    );
  }
}
